.container {
  background-image: linear-gradient(rgb(0, 57, 51, 0.8), rgba(0, 57, 51, 0.8)),
    url('../../assets/images/money.png');
  /* background-size: cover; */
  min-height: 100vh;
  width: 100vw;
  background-position: center center;
  padding: 10rem;
  padding-top: 6.4rem;
}
.headingContainer {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-top: 100px;
}
.heading {
  font-weight: 700;
  font-size: 28px;
  line-height: 46px;
  text-align: center;
  text-transform: capitalize;
  color: #f8f8f6;
  margin-bottom: 2rem;
  width: 60%;
}
.caption {
  font-weight: 300;
  font-size: 18px;
  line-height: 30px;
  text-align: center;
  color: #f8f8f6;
  margin-bottom: 90px;
  width: 50%;
}
.info {
  border-radius: 8px;
  padding: 5rem 6rem;
  background-color: #f8f8f6;
  display: flex;
}
.infoLeft {
  display: flex;
  flex-direction: column;
  flex: 80%;
  padding-right: 120px;
}

.infoRight {
  display: flex;
  flex-direction: column;
  flex: 20%;
}
.infoLeftHeading {
  display: flex;
  align-items: center;
  margin-bottom: 3rem;
}
.infoPersonalHeading {
  font-weight: 700;
  font-size: 24px;
  color: #005259;
  margin: 0;
  margin-right: 10px;
}
.switchActions {
  display: flex;
  align-items: center;
}
.label {
  font-size: 12px;
  color: #9f9f9f;
  font-weight: bold;
  margin-bottom: 5px;
}
.amounts {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.amountContainer {
  margin: 5px 0 14px;
}
.amount {
  border: 1px solid rgba(0, 82, 8, 0.9);
  border-radius: 8px;
  padding: 6px 12px;
  justify-content: center;
  align-items: center;
  display: flex;
  font-weight: bolder;
  cursor: pointer;
  color: #005259;
  font-size: 12px;
  margin-top: 6px;
}
.amountActive {
  color: #fff;
  background-color: #005259;
}
.amount:hover {
  opacity: 0.8;
  transform: scale(1.1);
  transition: 0.3s ease;
  background-color: #005259;
  color: #fff;
}
.linksText {
  color: #b7b6b5;
  font-weight: bold;
  font-size: 15px;
}
.socialIcons {
  display: flex;
  margin: 14px 0 8rem;
  align-items: center;
}
.recentText {
  color: #005259;
  font-size: 15px;
  font-weight: bolder;
  margin-bottom: 16px;
}
.hairLine {
}

@media screen and (max-width: 835px) {
  .container {
    padding: 5px;
  }
  .heading {
  font-size: 20px;
  line-height: 33px;
  }
  .caption {
    font-size: 15px;
    line-height: 25px;
  }

  .info {
    flex-direction: column;
    padding: 5rem 4rem;
  }
  .infoLeft, .infoRight {
    flex: 100%; /* Each will take full width */
    padding-right: 0; /* Remove the padding on smaller screens */
  }
  .linksText {
    display: none;
  }
  .socialIcons {
    display: none;  
  }

  .recentText {
    margin-top: 4rem; 
  }
}

@media screen and (max-width: 480px) {
  .heading{
    font-size: 18px;
  line-height: 23px;
  }
  .caption {
    line-height: 20px;
  }
}