.container {
  padding: 10px;
  border-radius: 8px;
  height: 150px;
  width: 200px;
  background-color: #fff;
}
.container:hover {
  opacity: 0.8;
  transform: scale(1.1);
  transition: 0.3s ease;
  background-color: #005259;
  color: #fff;
}
.image {
  height: 58%;
  width: 100%;
  border-radius: 8px;
}
.image img {
  width: 100%;
  height: 100%;
  border-radius: 8px;
  object-fit: cover;
  object-position: center;
}
.title {
  color: #9f9f9f;
  font-weight: bolder;
  margin-top: 12px;
  font-size: 12px;
}
.description {
  color: #9f9f9f;
  margin-top: 4px;
  font-size: 10px;
}


@media screen and (max-width: 835px) {
.container{
  display:inline-block;
  height: 200px; 
  width: 250px;
  margin: 20px;

}
}

@media screen and (max-width: 675px) {
  .container{
    display:inline-block;
    margin: 5px;
  }
  }

@media screen and (max-width: 620px) {
  .container {
    width: 100%;
    height: 250px;
  }
}